import Axios, { Methods } from "./http";

// 登录
// export const handleLogin = (username: string, password: string) =>
//   Axios()({ url: "login", data: { username, password } });
export const handleLogin = (username: string, password: string) =>
  Axios()({ url: `login?username=${username}&&password=${password}` });

// 根据token获取用户信息
export const handleToken = () => Axios()({ url: "token" });

// html列表
export const handleHtmlList = () => Axios()({ url: "html" });

// 文章详情
export const handleArticle = (key: string) =>
  Axios()({
    url: "article",
    data: { key },
  });
